'use client';

import { useSearchParams } from 'next/navigation';

export default function FlashMessage() {
  //クエリ文字列を取得
  const searchParams = useSearchParams();
  // パラメータを取得
  const success = searchParams.get('success');

  if (!success) return null;
  let message;

  switch (success) {
    case '1':
      message = 'ログインしました';
      break;
    case '2':
      message = '新規投稿しました';
      break;
  }
  return <div className="max-w-7xl mx-auto px-4 py-2 bg-green-100 text-black rounded-md shadow">{message}</div>;
}
